.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  height: 60px;
  padding: 10px;
  box-shadow: 0 0 2px 2px #eee;
  z-index: 2;
  position: sticky;
  top: 0;
}

.btn-menu {
  display: none;
}

@media (max-width: 414px) {
  .btn-menu {
    display: block;
  }
}