.form {
  width: 420px;
  margin: auto;
  height: 480px;
  background-color: #fff;
  box-shadow: 0 0 3px 3px #eee;
  border-radius: 10px;
  padding: 20px;
  margin-top: 10px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.logo {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

@media (max-width: 414px) {
  .form {
    border: none;
    width: auto;
    height: auto;
  }
}