.logo-title {
  display: flex;
  align-items: center;
}

.logo-title img {
  margin-right: 10px;
}

.title {
  text-transform: uppercase;
  color: var(--primary-color);
  font-size: 24px;
  margin-top: 10px;
  font-weight: 900;
}

@media (max-width: 414px) {
  .title {
    display: none;
  }
}